@font-face {
  font-family: TitiReg;
  src: url(../public/fonts/TitilliumWeb-Regular.ttf)
}

@font-face {
  font-family: GenosReg;
  src: url(../public/fonts/Genos-VariableFont_wght.ttf)
}

h3 {
  font-family: GenosReg;
}

.App {
  width: 100%;
  height: 635px;
  display: flex;
  font-size: 12px;
  font-family: sans-serif;
  text-align: center;
  background:linear-gradient(rgb(19, 2, 19),transparent);
  background-color:rgb(67, 15, 71);
}

.Background {
  margin-left: 24%;
  margin-top: 2%;
  display: flex;
  font-size: 12px;
  font-family: sans-serif;
  text-align: center;
}

.Card {
  position: absolute;
  width: 200px;
  height: 500px;
  background-color: #f2f2f2;
  background-image: url(img/bgresize.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.card-container {
  position: relative;
  /* max-width: 226px; */
  margin: 0 auto;
}

.global-mining-container {
  margin: auto;
  display: inline-block;
  vertical-align: middle;
  float: left;
  margin-left: 165px;
  padding: 2px;
}

.earth {
  margin-top: -0.8px;
  margin-right: 5px;
  opacity: 90%;
  border: 1px solid rgb(203, 203, 204);
  cursor: auto !important;
}

/*global mining button*/

.global-mining-button {
  font-family: GenosReg;
  font-size: 17px;
  background-color:rgb(57, 1, 59);
  border: 2px solid #aaa7a7;
  opacity: 75%;
}

.global-mining-button:hover {
  background-color: rgb(49, 5, 51);
  border: 2px solid #aaa7a7;
  box-shadow: 0px 0px 5px rgb(110, 32, 114);
}

.alert {
  position: absolute;
  margin-top: -50px;
  margin-left: -17%;
  height: 125px;
  width: 500px;
  background:linear-gradient(rgb(44, 25, 44),transparent);
  background-color:rgb(67, 15, 71);
  border: 2px solid #aaa7a7;
  color: white;
  box-shadow: 0px -5px 30px rgb(37, 36, 36);
}

.close-button {
  margin-top: -5px;
  background-color: rgb(31, 2, 32);
  border: 1px solid #aaa7a7;
  color: #FFF;
}

.close-button:hover {
  color: white;
  background-color:rgb(52, 15, 54);
  border: 1px solid #aaa7a7;
  box-shadow: 0px 0px 10px rgb(110, 32, 114);
}

img {
  border-radius: 500%;
  border: 1px solid black;
  margin-top: 15px;
  margin-left: 10px;
  float: left;
  box-shadow: 0px -1px 10px rgb(146, 144, 144);
}

img:hover {
  border: 1px solid rgb(203, 203, 204);
  cursor: pointer;
}

.paragraph {
  font-size: 8px;
  border: 1px solid rgb(241, 235, 235);
  background:linear-gradient(rgba(141, 141, 146, 0.5),transparent);
  background-color:rgb(29, 23, 23);
  color: white;
  height: 200px;
  padding: 1%;
}

/* appear - on page load */
.fade-appear {
  opacity: 0;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity 1000ms linear;
}

/*on enter */
.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms linear;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 1000ms linear;
}

.fade-exit-done {
  opacity: 0;
}

/* slide enter */
.slide-enter {
  opacity: 0;
  transform: scale(0.97) translateY(5px);
  z-index: 1;
}
.slide-enter.slide-enter-active {
  opacity: 1;
  transform: scale(1) translateY(0);
  transition: opacity 3000ms linear 1000ms, transform 3000ms ease-in-out 1000ms;
}

/* slide exit */
.slide-exit {
  opacity: 1;
  transform: scale(1) translateY(0);
}
.slide-exit.slide-exit-active {
  opacity: 0;
  transform: scale(0.97) translateY(5px);
  transition: opacity 1500ms linear, transform 1500ms ease-out;
}
.slide-exit-done {
  opacity: 0;
}

/*pagination customization*/

.pagination {
  margin-top: 220px;
  padding: 10px;
}

.page-items {
  padding: 1.4px;
}

.page-link {
  background-color: #948f9e;
  border-radius: 50%;
}

.page-link:hover {
  color: black;
}

/* table style */

.table {
  background-color: rgb(214, 214, 214);
  width: 38%;
  margin-left: 378px;
  box-shadow: 0px -5px 30px rgb(105, 99, 99);
}

td {
  text-align: left;
}

/* global mining alert transitions */

.alert-enter {
  opacity: 0;
  transform: scale(0.2);
}
.alert-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 200ms, transform 200ms;
}
.alert-exit {
  opacity: 1;
}

.alert-exit-active {
  opacity: 0;
  transform: scale(0.5);
  transition: opacity 200ms, transform 200ms;
}

.mining {
  font-family: GenosReg;
  font-size: 15px;
}

.alert-heading {
  font-family: GenosReg;
  font-size: 25px;
}

